.App {
  text-align: center;
}
.black-nav {
  background: #000;
  display: flex;
  color: #fff;
  padding: 10px 20px;
  justify-content: flex-start;
}

div {
  box-sizing: border-box;
}
.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}
.like {
  display: inline-block;
  cursor: pointer;
  transition: 0.1;
}
.like:active {
  transform: scale(0.9);
}
.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
button {
  margin: 10px 0;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
